  keyPress1(event: any) {
    const pattern = /^[a-zA-Z ]+$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && event.keyCode != 9 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  //document UPLOAD FOR Document
  handleImageDocument(file) {
    var picReader = new FileReader();
    picReader.onload = (e: any) => {
      var reader = e.target;
      this.imagePreviewDocument = reader.result;
    }
    picReader.readAsDataURL(file)
  }

  selectDocument(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      if (event.target.files[0].type === "application/pdf") {
        var file = event.target.files[0];
        this.img1 = true;
        this.progressShow1 = false;
        this.progressHide1 = false;
        this.file2Upload1 = event.target.files[0];
        this.handleImageDocument(file)
      } else {
        event.target.value = ""
        alert('Please Select PDF')
        this.img1 = false;
        this.progressShow1 = false;
        this.progressHide1 = false;
      }
    }
    else {
      this.img1 = false;
      this.progressShow1 = false;
      this.progressHide1 = false;
      this.imagePreviewDocument = '../../assets/img/no-image.png'
    }
  }

  fileUploadDocument() {
    const url = Global.url + '/imageUploadData'
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append('image', this.file2Upload1)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      }
    }
    xhr.open('post', url, true);
    xhr.upload.onprogress = (e) => {
      if (e.lengthComputable) {
        this.percentage1 = (e.loaded / e.total) * 100;
        this.progressShow1 = true;
        this.progressHide1 = false;
        if (this.percentage1 === 100) {
          this.progressShow1 = false;
          this.progressHide1 = true;
          // this.imageUploadVariable.nativeElement.value = "";
          this.img1 = false;
        }
      }
    }
    xhr.onerror = function (e) {

    };
    xhr.onload = function () {
      var a = JSON.parse(xhr.responseText);
      Global.secimagePath = a.imagePath
      Global.documentName = a.name
    };
    xhr.send(formData);
  }
