  onEndDateSelected(endDate) {
    this.startMax = endDate;
    const startTime = this.addWorkOrder.value.startTime;
    const endTime = this.addWorkOrder.value.endTime;
    if (startTime) {
      this.onStartTime(startTime);
    }
    if (endTime) {
      this.onEndTime(endTime);
    }
  }

  onStartTime(startTime) {
    this.endDate()
    if (this.addWorkOrder.value.addPrimaryTech == "NONE") {
      return
    }
    this.workOrderPrimaryTech()
    this.getStaffTimeSheduleday()

    console.log(startTime)
    const startDate = this.addWorkOrder.value.startDate;
    const endDate = this.addWorkOrder.value.endDate;
    const endTime = this.addWorkOrder.value.endTime;
    if (endDate && endTime) {
      const startDateTime = moment(startDate + startTime, 'YYYY-MM-DDhh:mm');
      const endDateTime = moment(endDate + endTime, 'YYYY-MM-DDhh:mm');
      const isAfter = moment(startDateTime).isAfter(endDateTime);
      const isBefore = moment(startDateTime).isBefore(endDateTime);
      // console.log("isAfter" + isAfter)
      // console.log("isBefore" + isBefore)
      var time = new Date().getHours() + ':' + new Date().getMinutes()
      if (isAfter === false && isBefore === true) {
        this.startTimeErrorMsg = 'Invalid Start Time';
        ;
      } else {
        this.startTimeErrorMsg = '';
      }
      if (moment(startTime, "HH:mm") < moment(time, "HH:mm")) {
        console.log('THe current time is greater than actual time')
        this.startTimeErrorMsg = 'Invalid Start Time';
      } else {
        this.startTimeErrorMsg = '';
      }
    }

  }

  onEndTime(endTime) {
    if (this.addWorkOrder.value.addPrimaryTech == "NONE") {
      return
    }
    this.workOrderPrimaryTech()
    this.getStaffTimeSheduleday()
    const startDate = this.addWorkOrder.value.startDate;
    const startTime = this.addWorkOrder.value.startTime;
    const endDate = this.addWorkOrder.value.endDate;
    if (startDate && startTime) {
      const startDateTime = moment(startDate + startTime, 'YYYY-MM-DDhh:mm');
      const endDateTime = moment(endDate + endTime, 'YYYY-MM-DDhh:mm');
      const isAfter = moment(startDateTime).isAfter(endDateTime);
      if (isAfter) {
        this.endTimeErrorMsg = 'Invalid End Time';
      } else {
        this.endTimeErrorMsg = '';
        this.startTimeErrorMsg = '';
      }
    }
  }
