  primaryTechChange() {
    if (this.addWorkOrder.value.addPrimaryTech == "NONE") {
      this.employeeRoleDataDisplay = this.employeeRoleData
      this.staffData.workTechName = "Tech will be Assigned Shortly"
      this.displayPrimaryTech = ""
      return false;
    }

    if (this.addWorkOrder.value.addPrimaryTech != "NONE" || this.addWorkOrder.value.addPrimaryTech != "" || this.addWorkOrder.value.addPrimaryTech != undefined || this.addWorkOrder.value.addPrimaryTech != null) {
      this.displayPrimaryTech = this.addWorkOrder.value.addPrimaryTech.split('-')[0] + '-' + this.addWorkOrder.value.addPrimaryTech.split('-')[1] + '-' + this.addWorkOrder.value.addPrimaryTech.split('-')[3] + '-' + this.addWorkOrder.value.addPrimaryTech.split('-')[2].split('.')[0] + " " + this.addWorkOrder.value.addPrimaryTech.split('-')[2].split('.')[1]
      this.employeeIdForService = this.addWorkOrder.value.addPrimaryTech.split('-')[0] + '-' + this.addWorkOrder.value.addPrimaryTech.split('-')[1]
      this.employeeRoleDataDisplay = this.employeeRoleData.filter(val => val.employeeId != this.addWorkOrder.value.addPrimaryTech.split('-')[0] + '-' + this.addWorkOrder.value.addPrimaryTech.split('-')[1])
      this.workOrderPrimaryTech()
      this.getStaffTimeSheduleday()
      this.multipleTechsData.forEach((e) => {
        if (e.employeeId == this.addWorkOrder.value.addPrimaryTech.split('-')[0] + '-' + this.addWorkOrder.value.addPrimaryTech.split('-')[1]) {
          e.checked = false
        }
      })
      this.multipleTechsData = this.multipleTechsData.filter(val => val.employeeId != this.addWorkOrder.value.addPrimaryTech.split('-')[0] + '-' + this.addWorkOrder.value.addPrimaryTech.split('-')[1])
      console.log(this.addWorkOrder.value.addPrimaryTech.split('-')[0])
      console.log(this.employeeRoleDataDisplay)
      var employeeData = this.employeeRole.filter(val => val.employeeId == this.addWorkOrder.value.addPrimaryTech.split('-')[0] + '-' + this.addWorkOrder.value.addPrimaryTech.split('-')[1])
      if (employeeData.length > 0) {
        this.staffData.consultantId = employeeData[0].id
        this.staffData.departmentId = employeeData[0].empgroup
        this.staffData.emailId = employeeData[0].email
        this.staffData.mobileNumber = employeeData[0].mobilePhone
        this.staffData.workTechName = this.addWorkOrder.value.addPrimaryTech.split('-')[2].split('.')[0] + " " + this.addWorkOrder.value.addPrimaryTech.split('-')[2].split('.')[1]
        console.log(this.staffData)
      }
    }
  }


  workorderData(x) {
    const formData = new FormData();
    formData.append("jobTitle", this.addWorkOrder.value.jobTitle);
    formData.append("estimatedHours", this.addWorkOrder.value.estimatedHours);
    formData.append("description", this.addWorkOrder.value.description);
    formData.append("startDate", this.addWorkOrder.value.startDate);
    formData.append("endDate", this.addWorkOrder.value.endDate);
    formData.append("startTime", this.addWorkOrder.value.startTime);
    formData.append("endTime", this.addWorkOrder.value.endTime);
    formData.append("selectCategory", this.addWorkOrder.value.selectCategory);
    formData.append("selectSubCategory", this.addWorkOrder.value.selectSubCategory);
    formData.append("source", this.addWorkOrder.value.source);
    formData.append("paymentMethod", this.addWorkOrder.value.paymentMethod);
    formData.append("promoCodeDisc", this.addWorkOrder.value.promoCodeDisc);
    formData.append("cost", this.addWorkOrder.value.cost == "" ? 0 : this.addWorkOrder.value.cost);
    formData.append("makeReoccuring", this.addWorkOrder.value.makeReoccuring);
    formData.append("packageType", this.addWorkOrder.value.packageType);
    // formData.append("c", this.addWorkOrder.value.alertTechBefore);

    formData.append("image", this.imagePreviewDocument);
    console.log(formData)
    // console.log("this obj data" + "ram");
    this.http.post(Global.url + "/addWorkOrderInfo", formData).subscribe(
      data => {
        if (data.status === 200) {
          this.spinner.hide();
          console.log("Add Work Order-Save Success")
          this.toastr.success("Added Add workorder Data Successfully");
          Global.imagePath = "";
          this.router.navigate(["/listevent"]);
        } else {
          this.spinner.hide();
          console.log("Add Work Order-Save Failed")
          this.toastr.warning("Unable to Connect to Server");
        }
      },
      error => {
        this.spinner.hide();
        console.log("Hide Spinner-4 ::: Work Order Data Error")
        this.toastr.warning("Unable to Connect to Server");
      }
    );
  }

