  //GET ALL SERVICE ADDRESSES
  getAllServiceAddresses(customerId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/fetchServiceAddressesForWorkOrder/' + customerId, options)
      .subscribe(data => {
        var serviceAddressData = data.json();
        if (serviceAddressData.status == 200) {
          this.workorderServiceAddresses = serviceAddressData.response;
          this.workorderServiceAddresses.forEach((e,i)=>{
            e.disableInput = true;
          })
          this.serviceAddressStatus = true
          return false;
        } else {
          this.serviceAddressStatus = false
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
