  // PRO DETAILS
  getProDetails(proCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzProfile/' + proCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.proData = response.response;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  // PRO DETAILS
  //SECONDARY AND EXCLUDE TECHS SELECTION DONE MODAL CONFIRMATION
  done() {
    this.multipleTechsDataDisplay = this.employeeRoleData.filter(val => val.checked == true)
  }
  //SECONDARY AND EXCLUDE TECHS SELECTION DONE MODAL CONFIRMATION

  selectEmployee(data) {
    console.log(data)
    for (var i = 0; i < this.employeeRoleData.length; i++) {
      if (data.employeeId == this.employeeRoleData[i].employeeId) {
        this.employeeRoleData[i].checked = data.checked
      }
    }
  }



  //TASK #574: getListOfWOMulitpleTechs starts here
  getListOfWOMulitpleTechs() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfWOMulitpleTechs/' + this.loginData.loginCode + '/' + this.billingAddressData.customerId + '/' + this.billingAddressData.workOrderNumber, options)
      .subscribe(data => {
        var response = data.json();
        console.log(response)
        if (response.status == 200) {
          this.multipleTechsData = response.response
          console.log(this.multipleTechsData)
          for (var i = 0; i < this.multipleTechsData.length; i++) {
            this.multipleTechsData[i].checked = true
            for (var j = 0; j < this.employeeRoleData.length; j++) {
              if (this.employeeRoleData[j].employeeId == this.multipleTechsData[i].employeeId) {
                this.employeeRoleData[j].techType = this.multipleTechsData[i].techType
                this.employeeRoleData[j].checked = true
              }
            }
          }
          this.done()
          return false;
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //TASK #574: getListOfWOMulitpleTechs ends here

  //TASK #574: deleteAndInsertMultipleTechs starts here
  deleteAndInsertMultipleTechs(data) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.multipleTechsDataDisplay.forEach((e) => {
      e.workOrderNumber = data.workOrderNumber
      e.customerId = data.customerId
      e.createdOn = this.billingAddressData.createdOn
      e.modifiedOn = `${Utility.year}-${Utility.month}-${Utility.day} ${Utility.hour}:${Utility.min}:${Utility.seconds}`
    })
    var deleteAndInsertMultipleTechsObj = {
      "proId": data.proId,
      "customerId": data.customerId,
      "workOrderNumber": data.workOrderNumber,
      "techs": this.multipleTechsDataDisplay,
    }
    this.spinner.hide();
    this.http.post(Global.url + '/deleteAndInsertMultipleTechs', deleteAndInsertMultipleTechsObj, options)
      .subscribe(data => {
        var response = data.json();
        console.log(response)
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
