  //VIEW CUSTOMER DETAILS
  setAddress() {
    console.log("in set adress")
    let control = <FormArray>this.addAddressList.controls.fromAddressValues;
    console.log("length" + this.serviceAddressData1.length)
    this.serviceAddressData.forEach((x, i) => {
      console.log("setx" + JSON.stringify(x))
      control.push(this.formBuilder.group({
        firstName: x.firstName,
        lastName: x.lastName,
        emailId: x.emailId,
        serviceAddressPhoneNumber: x.phoneNumber,
        phoneType: x.phoneType,
        propertyName: x.propertyName,
        address: x.address,
        unit: x.unit,
        zipCode: x.zipCode,
        city: x.city,
        state: x.state,
        adminNotes: x.adminNotes,
        emergency: x.emergency,
        reCall: x.reCall,
        preWorkChecklistNeeded: x.preWorkChecklistNeeded,
        postWorkChecklistNeeded: x.postWorkChecklistNeeded,
        sameAsBillingCheck: x.sameAsBillingCheck
      }))
      if (i == this.serviceAddressData.length - 1) {
        return control;
      }
    })
  }

