  //----------------------LAT LONG FOR SERVICE ADDRESS---------
  update() {
    if (this.startTimeErrorMsg == 'Invalid Start Time' || this.endTimeErrorMsg == 'Invalid End Time') { return; }
    if (this.shuduleTiming == "INVALID") {
      console.log("INVALID CONDITION IS CALIING")
      $("#myModal0").modal("show");
      // alert("The technician( " + this.editWorkOrder.value.addPrimaryTech + " ) has already been assigned  /  overlap  during this time slot (" + this.editWorkOrder.value.startTime + " -- " + this.editWorkOrder.value.endTime + "). Please choose a different time.")
    }
    else {
      this.spinner.show()
      this.addAddressList.value.fromAddressValues.forEach((e, i) => {
        var addressMixed = e.address + "," + e.city + ',' + e.state + "," + e.zipCode
        this.geocodingService.getLatLongViaZipcode(addressMixed)
          .subscribe(address => {
            if (address == undefined || address == '') {
              e.latitude = 0
              e.longitude = 0
            } else {
              e.latitude = address.lat
              e.longitude = address.lng
            }
            // google.maps.event.addDomListener(window, 'load', this.initializeZipMap(coordObj));
          })
        if (i == this.addAddressList.value.fromAddressValues.length - 1) {
          this.getLatlongforBilling()
        }
      })
    }


  }
  //----------------------LAT LONG FOR SERVICE ADDRESS---------
  //----------------------LAT LONG FOR BILLING ADDRESS---------
  getLatlongforBilling() {
    var addressMixed = this.editWorkOrder.value.customerAddress + "," + this.editWorkOrder.value.customerCity + ',' + this.editWorkOrder.value.customerState + "," + this.editWorkOrder.value.customerZipCode
    this.geocodingService.getLatLongViaZipcode(addressMixed)
      .subscribe(async (address) => {
        if (address == undefined || address == '') {
          this.toastr.info('No Latitude and Longitude for this address')
          let latitude = 0
          let longitude = 0
          let getServiceAreaTimeZone = await this.getServiceAreaTimezone(latitude, longitude)
          if (getServiceAreaTimeZone['status'] == 200) {
            this.serviceTimeZone = getServiceAreaTimeZone['response']['timeZoneName']
            await this.updateWorkOrderTimezone(getServiceAreaTimeZone['response'])
            this.updateAddress(latitude, longitude)
          } else {
            this.updateAddress(latitude, longitude)
          }
        } else {
          let latitude = address.lat
          let longitude = address.lng
          let getServiceAreaTimeZone = await this.getServiceAreaTimezone(latitude, longitude)
          if (getServiceAreaTimeZone['status'] == 200) {
            this.serviceTimeZone = getServiceAreaTimeZone['response']['timeZoneName']
            await this.updateWorkOrderTimezone(getServiceAreaTimeZone['response'])
            this.updateAddress(latitude, longitude)
          } else {
            this.updateAddress(latitude, longitude)
          }
        }
        // google.maps.event.addDomListener(window, 'load', this.initializeZipMap(coordObj));
      })
  }
  //----------------------LAT LONG FOR BILLING ADDRESS---------
  //GET SERVICE AREA TIMEZONE
  getServiceAreaTimezone(latitude, longitude) {
    return new Promise((resolve, reject) => {
      let serviceAreaObj = {};
      serviceAreaObj['latitude'] = latitude;
      serviceAreaObj['longitude'] = longitude;
      serviceAreaObj['timestamp'] = (new Date().getTime() / 1000);
      this.http.post(Global.url + '/getServiceAreaTimeZone', serviceAreaObj)
        .subscribe(data => {
          resolve(data.json())
        })
    })
  }
  //GET SERVICE AREA TIMEZONE
  //UPDATE WORK ORDER TIMEZONE
  updateWorkOrderTimezone(serviceAreaTimezone) {
    return new Promise((resolve, reject) => {
      let woTimezoneObj = {};
      woTimezoneObj['woNumber'] = this.billingAddressData.workOrderNumber
      woTimezoneObj['dstOffset'] = serviceAreaTimezone.dstOffset
      woTimezoneObj['status'] = serviceAreaTimezone.status
      woTimezoneObj['timeZoneId'] = serviceAreaTimezone.timeZoneId
      woTimezoneObj['timeZoneName'] = serviceAreaTimezone.timeZoneName
      woTimezoneObj['rawOffset'] = serviceAreaTimezone.rawOffset
      this.http.post(Global.url + '/workOrderTimeZoneUpdate', woTimezoneObj)
        .subscribe(data => {
          resolve(data.json())
        })
    })
  }
