  /*Display primary Tech*/
  getPrimaryTechs() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/fetchPrimaryTechWO/' + this.loginData.loginCode + '/' + this.billingAddressData.workOrderNumber + '/' + this.billingAddressData.customerId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.displayPrimaryTech = response.response[0]
          this.editWorkOrder.get('addPrimaryTech').setValue(this.displayPrimaryTech.employeeId + '-' + this.displayPrimaryTech.firstName + '.' + this.displayPrimaryTech.lastName + '-' + this.displayPrimaryTech.role);
          this.sendPrimaryTech = this.displayPrimaryTech.employeeId
          this.employeeIdForService = this.displayPrimaryTech.employeeId
          this.staffData.consultantId = this.displayPrimaryTech.consultantId
          this.staffData.departmentId = this.displayPrimaryTech.departmentId
          this.staffData.workTechName = this.displayPrimaryTech.workTechName
          this.staffData.emailId = this.displayPrimaryTech.email
          this.staffData.mobileNumber = this.displayPrimaryTech.mobilePhone
          this.primaryTechStatus = true
          return false;
        } else {
          this.editWorkOrder.get('addPrimaryTech').setValue('NONE');
          this.sendPrimaryTech = ''
          this.employeeIdForService = ''
          this.staffData.consultantId = ''
          this.staffData.departmentId = ''
          this.staffData.workTechName = ''
          this.staffData.emailId = ''
          this.staffData.mobileNumber = ''
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
