  onEndTime(endTime) {
    if (this.editWorkOrder.value.addPrimaryTech == "NONE") {
      return
    }
    this.workOrderPrimaryTech()
    this.getStaffTimeSheduleday()
    const startDate = this.editWorkOrder.value.startDate;
    const startTime = this.editWorkOrder.value.startTime;
    const endDate = this.editWorkOrder.value.endDate;
    if (startDate && startTime) {
      const startDateTime = moment(startDate + startTime, 'YYYY-MM-DDhh:mm');
      const endDateTime = moment(endDate + endTime, 'YYYY-MM-DDhh:mm');
      const isAfter = moment(startDateTime).isAfter(endDateTime);
      if (isAfter) {
        this.endTimeErrorMsg = 'Invalid End Time';
      } else {
        this.endTimeErrorMsg = '';
        this.startTimeErrorMsg = '';
      }
    }
  }
  /* --/Start Time and Date time validation */
  /*  --Date Day Convertion */
  myFunction() {
    var d = new Date(this.editWorkOrder.value.startDate);
    var weekday = new Array(7);
    weekday[0] = "Sunday";
    weekday[1] = "Monday";
    weekday[2] = "Tuesday";
    weekday[3] = "Wednesday";
    weekday[4] = "Thursday";
    weekday[5] = "Friday";
    weekday[6] = "Saturday";
    this.n = weekday[d.getDay()];
    console.log("Start DAY ::" + this.n)
  }

  endDate() {
    var d = new Date(this.editWorkOrder.value.endDate);
    var weekday = new Array(7);
    weekday[0] = "Sunday";
    weekday[1] = "Monday";
    weekday[2] = "Tuesday";
    weekday[3] = "Wednesday";
    weekday[4] = "Thursday";
    weekday[5] = "Friday";
    weekday[6] = "Saturday";
    this.endDay = weekday[d.getDay()];
    console.log(" End DAY :: " + this.endDay)
  }
