  /* --Get staffshuduleTimings Data --*/

  getStaffTimeSheduleday() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.post(Global.url + '/getEmpTimeSheduleDay/' + this.employeeIdForService + "/" + this.n, options)
      .subscribe(data => {
        var response = data.json();
        // console.log("response" + JSON.stringify(response))
        if (response.status == 200) {
          this.staffTimeShedule = response.response

          console.log(" StaffTimeShedule data" + JSON.stringify(this.staffTimeShedule))

          this.woDay = this.staffTimeShedule[0].day
          this.staffId = this.staffTimeShedule[0].employeeId
          this.empstartTime = this.staffTimeShedule[0].fromTime
          // console.log("empendTime" + JSON.stringify(this.empendTime))
          this.empendTime = this.staffTimeShedule[0].toTime
          // console.log("empstartTime" + JSON.stringify(this.empstartTime))
          console.log("this.editWorkOrder.value.startTime" + this.editWorkOrder.value.startTime)
          console.log("this.editWorkOrder.value.endTime" + this.editWorkOrder.value.endTime)
          // if (this.empendTime == " PM") {
          //   console.log("PMMMMM")

          // }
          // this.time = this.staffTimeShedule[0].toTime
          // // this.ampm('time')
          console.log("ENDTIME" + JSON.stringify(this.editWorkOrder.value.startTime > this.staffTimeShedule[0].toTime + "next" + this.editWorkOrder.value.startTime > this.staffTimeShedule[0].fromTime))
          if (this.editWorkOrder.value.startTime > this.staffTimeShedule[0].toTime) {
            // alert("Wo StartTime is " + this.editWorkOrder.value.startTime + " " + ">" + " Employee Time Card Settings  EndTime  is" + this.staffTimeShedule[0].toTime + " ")
          }

          else if (this.editWorkOrder.value.startTime < this.staffTimeShedule[0].fromTime) {
            // alert("Wo StartTime is " + this.editWorkOrder.value.startTime + " " + "<" + " Employee Time Card Settings  startTime  is" + this.staffTimeShedule[0].fromTime + " ")
          }
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

