  //SELECT SERVICE ADDRESS
  selectServiceAddress(check) {
    console.log(check)
    this.editWorkOrder.get('servData').setValue(false)
    if (check == true) {
      this.workorderServiceAddresses.forEach((e, i) => {
        e.disableInput = check;
        e.id = i + 1
      })
      this.employees.enable()
      this.employees.patchValue([
        {
          propertyName: '',
          firstName: '',
          lastName: '',
          serviceAddressPhoneNumber: '',
          emailId: '',
          phoneType: '',
          address: '',
          unit: '',
          city: '',
          state: '',
          zipCode: '',
          sameAsBillingCheck: this.editWorkOrder.value.servData,
          adminNotes: '',
        }
      ]);
    } else {
      this.workorderServiceAddresses.forEach((e, i) => {
        e.disableInput = check;
        e.id = i + 1
      })
      this.employees.disable()
      this.employees.patchValue([
        {
          propertyName: '',
          firstName: '',
          lastName: '',
          serviceAddressPhoneNumber: '',
          emailId: '',
          phoneType: '',
          address: '',
          unit: '',
          city: '',
          state: '',
          zipCode: '',
          sameAsBillingCheck: this.editWorkOrder.value.servData,
          adminNotes: '',
        }
      ]);
    }
  }
  //SELECT SERVICE ADDRESS
  //GET ALL SERVICE ADDRESSES
  getAllServiceAddresses(customerId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/fetchServiceAddressesForWorkOrder/' + customerId, options)
      .subscribe(data => {
        var serviceAddressData = data.json();
        if (serviceAddressData.status == 200) {
          this.workorderServiceAddresses = serviceAddressData.response;
          this.workorderServiceAddresses.forEach((e) => {
            e.disableInput = true;
          })
          this.serviceAddressStatus = true
          return false;
        } else {
          this.serviceAddressStatus = false
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
