import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddWorkOrderComponent } from './proz-workorder/add-work-order/add-work-order.component';
import { ListWorkOrderComponent } from './proz-workorder/list-work-order/list-work-order.component';
import { ViewWorkOrderComponent } from './proz-workorder/view-work-order/view-work-order.component';
import { EditWorkOrderComponent } from './proz-workorder/edit-work-order/edit-work-order.component';
import { AddNewCreditCardComponent } from './proz-workorder/add-new-credit-card/add-new-credit-card.component';
import { CreateInspectionSheetComponent } from './proz-workorder/create-inspection-sheet/create-inspection-sheet.component';
import { ProzWorkOrderLayoutComponent } from './proz-work-order-layout/proz-work-order-layout.component';

const routes: Routes = [
  {
    path: '', component: ProzWorkOrderLayoutComponent,
    children: [

      {
        path: 'addWorkOrder/:customerId',
        component: AddWorkOrderComponent
      },
      {
        path: 'listWorkOrders',
        component: ListWorkOrderComponent
      },
      // {
      //   path: 'editWorkOrder',
      //   component: EditWorkOrderComponent
      // },
      {
        path: 'editWorkOrder/:workOrderNumber',
        component: EditWorkOrderComponent
      },
      {
        path: 'viewWorkOrder/:workOrderNumber',
        component: ViewWorkOrderComponent
      },
      {
        path: 'addNewCreditCard',
        component: AddNewCreditCardComponent
      },
      {
        path: 'createInspectionSheet',
        component: CreateInspectionSheetComponent
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzWorkOrderRoutingModule { }
