            this.woStatusComplete = true
            this.pauseRestartWo = true
            this.woEstimateComplte = true
            this.woStatusDataComplete = true;
            this.CreateDisabled = true;
            this.BookEquipmentDisabledComplete = true;
            this.disableStatusfalse = true;
            console.log('woStatusComplete' + JSON.stringify(this.woStatusComplete))

