  getRoles() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getStaffRoleProSpe' + "/" + this.loginData.loginCode, options)
      .subscribe(data => {
        var roles = data.json();
        if (roles.status == 200) {
          this.roleData = roles.response;
          ////console.log("role data" + JSON.stringify(this.roleData))
          this.dataRoles = true;
          ////console.log("service category Data" + JSON.stringify(this.roleData))
        }
        else {
          this.dataRoles = false;
        }
      })
  }


  changeRole(roleName) {
    ////console.log(roleName)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getStaffNameUsingRolesProSpe/' + roleName + "/" + this.loginData.loginCode, options).subscribe(data => {
      var response = data.json();
      if (response.status == 200) {
        this.staffData = response.response;
        // this.editCustomLaborCost.get("staffName").setValue(this.viewCustomLaborData.staffName)
        //console.log("staff Data" + JSON.stringify(this.staffData))
      } else {
        this.staffData = []

        this.toastr.warning("No Staff For the selected Role")
      }
    }, error => {
      this.toastr.warning("Unable to connect to server")
    })
  }

  getDetails(employeeId) {
    //console.log(employeeId)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getStaffDetailsUsingId/' + employeeId, options).subscribe(data => {
      var response = data.json();
      if (response.status == 200) {
        this.staffNameData = response.response[0];
        //console.log("staffName Data" + JSON.stringify(this.staffNameData))
        this.laborHours = "Hours",
          this.staffFirstName = this.staffNameData.firstName + "-" + this.staffNameData.lastName
        this.laborCost = this.staffNameData.billingRateperHr

        //  this.staffQuantity = "1"
        //  this.getQuantityValue(this.staffQuantity) 
        //  this.subTotalValue = this.staffQuantity * this.laborCost
      } else {
        this.toastr.warning("No Staff For the selected Role")
      }
    }, error => {
      this.toastr.warning("Unable to connect to server")
    })
  }


