  addCustomLaborFieldValue() {
    this.items = this.addCustomLaborMaterial.get('customLaborMaterial') as FormArray;
    this.items.push(this.createLaborMaterial());
  }


  createLaborMaterial(): FormGroup {
    var val = Math.floor(100 + Math.random() * 900);
    this.randamCLMvalue = "CLM" + "-" + val
    return this.formBuilder.group({
      laborDate: '',
      laborType: '',
      laborStaffName: '',
      laborQuantity: 0,
      laborUnits: '',
      laborDescription: '',
      laborStaffId: '',
      laborSubTotal: 0,
    });
  }

  addOtherFieldValue() {
    this.items = this.addOtherMaterial.get('otherMaterial') as FormArray;
    this.items.push(this.createOtherMaterial());
  }
  get otherMaterialValues(): FormArray {
    return this.addOtherMaterial.get('otherMaterial') as FormArray;
  }

  createOtherMaterial(): FormGroup {
    return this.formBuilder.group({
      date: '',
      description: '',
      quantity: 0,
      units: '',
      subTotal: '',
    });
  }

  submitOtherMaterial() {
    for (var i = 0; i < this.addOtherMaterial.get('otherMaterial').value.length; i++) {
      let Obj = {
        "proId": this.billingAddressData.proId,
        "workOrderNumber": this.workOrderNumber,
        "type": "Other",
        "date": '' + this.addOtherMaterial.get('otherMaterial').value[i].date,
        "description": '' + this.addOtherMaterial.get('otherMaterial').value[i].description,
        "quantity": '' + this.addOtherMaterial.get('otherMaterial').value[i].quantity,
        "units": '' + this.addOtherMaterial.get('otherMaterial').value[i].units,
        "subTotal": '' + this.addOtherMaterial.get('otherMaterial').value[i].subTotal,
      }
      //console.log("obj" + JSON.stringify(Obj))
      this.http.post(Global.url + '/addJobRunningCostOtherService/', Obj)
        .subscribe(data => {
          var addotherMaterilData = data.json();
          this.otherMaterialValues.reset()
          this.otherMaterialList();
          this.otherMaterialResponse = true;

        },
          error => {
            this.otherMaterialResponse = false;
            ////console.log('The execution is failed ' + JSON.stringify(error))
          })
    }
  }

  calculateOtherValue() {
    this.otherListValue = 0;
    for (var i = 0; i < this.viewOtherDetails.length; i++) {
      this.otherListValue = this.otherListValue + parseInt(this.viewOtherDetails[i].subTotal)
    }
  }

  otherMaterialList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/JobRunningCostListOtherService/' + this.loginData.loginCode + '/' + this.workOrderNumber, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.viewOtherDetails = response.response;
          this.otherListValue = 0
          this.calculateOtherValue()
          this.otherMaterialResponse = true;

        }
        else {
          this.otherMaterialResponse = false;
        }
        error => {
        }
      })
  }

